import React, { Component } from 'react';
import { Card, CardBody, Row, Col } from "reactstrap";
import { BaseUrl } from "../../helpers/Baseurl"

//Import Charts
import ReactApexChart from 'react-apexcharts';
import "./dashboard.scss";

class SalesAnalytics extends Component {
    constructor(props) {
        super(props);
        this.state = {
            series: [1,1, 1],
            options: {
                labels: ["Bidding", "Delivered", "Sold" ,"Lost", "Completed"],
                plotOptions: {
                    pie: {
                        donut: {
                            size: '70%'
                        }
                    }
                },
                dataLabels: {
                    enabled: false
                },
                legend: {
                    show: false,
                },
                colors: ['#1cbb8c', '#eeb902','#58a5ff','#5664d2', '#ff3d60'],

            }
        }
    }   

    componentDidCatch(error) {
        // Changing the state to true
        // if some error occurs
        console.log(error)
      }
    async componentDidMount() {
 
        const requestOptions = {
               method: 'GET',
               headers: { 'Content-Type': 'application/json' },
           };
           await fetch(`${BaseUrl}/api/jobs/dashboard/`,requestOptions)
               .then(res => res.json())
               .then(
                   (data) => {

                    // let data_length = data.length
                    // let bidding = 0
                    // let delivered = 0
                    // let sold = 0
                    // let completed = 0
                    // for (let i=0;i<data_length; i++ ){
                    //     if(data[i].status === "bidding"){
                    //      bidding = bidding+1 
                    //     }else if (data[i].status === "delivered"){
                    //      delivered = delivered +1 
                    //     }else if (data[i].status === "sold"){
                    //      sold = sold +1 
                    //      }else {
                    //      completed = completed+1
                    //     }
                    // }
                       let bidding_per = parseInt((data.bidding *100)/data.job_total)
                       let delivered_per = parseInt((data.delivered *100)/data.job_total)
                       let sold_per = parseInt((data.sold *100)/data.job_total)
                       let lost_per = parseInt((data.lost *100)/data.job_total)
                       let completed_per = parseInt((data.completed *100)/data.job_total)
                        this.setState({series: [bidding_per, delivered_per, sold_per,lost_per, completed_per]})
                   },
                   (error) => {
                       console.log(error, 'error--------------') 
                   }
               )  
     }


    render() {
    
        return (
            <React.Fragment>
                <Card>
                    <CardBody>
                       
                        <h4 className="card-title mb-4">Jobs Analytics</h4>

                        <div id="donut-chart" className="apex-charts">
                            <ReactApexChart options={this.state.options} series={this.state.series} type="donut" height="250" />
                        </div>

                        <Row>
                            <Col xs={2}>
                                <div className="text-center mt-4">
                                    <p className="mb-2 text-truncate"><i className="mdi mdi-circle text-success font-size-10 me-1"></i> Bidding</p>
                                    <h5>{this.state.series[0]} %</h5>
                                </div>
                            </Col>
                            <Col xs={3}>
                                <div className="text-center mt-4">
                                    <p className="mb-2 text-truncate"><i className="mdi mdi-circle text-warning font-size-10 me-1"></i> Delivered</p>
                                    <h5>{this.state.series[1]} %</h5>
                                </div>
                            </Col>
                            <Col xs={2}>
                                <div className="text-center mt-4">
                                    <p className="mb-2 text-truncate"><i className="mdi mdi-circle text-info font-size-10 me-1"></i> Sold</p>
                                    <h5>{this.state.series[2]} %</h5>
                                </div>
                            </Col>
                            <Col xs={2}>
                                <div className="text-center mt-4">
                                    <p className="mb-2 text-truncate"><i className="mdi mdi-circle text-primary font-size-10 me-1"></i> Lost</p>
                                    <h5>{this.state.series[3]} %</h5>
                                </div>
                            </Col>
                            <Col xs={3}>
                                <div className="text-center mt-4">
                                    <p className="mb-2 text-truncate"><i className="mdi mdi-circle text-danger font-size-10 me-1"></i> Completed</p>
                                    <h5>{this.state.series[4]} %</h5>
                                </div>
                            </Col>
                        </Row>
                    </CardBody>
                </Card>
            </React.Fragment>
        );
    }
}

export default SalesAnalytics;