import React, { Component } from "react";
import { Container, Row, Col,Card, CardBody, CardTitle } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from '../../components/Common/Breadcrumb';
import { BaseUrl } from "../../helpers/Baseurl"

//Import Components
import MiniWidgets from "./MiniWidgets";
import RevenueAnalytics from "./RevenueAnalytics";
import SalesAnalytics from "./SalesAnalytics";
import EarningReports from "./EarningReports";
import SoldByUser from "./SoldByUser"
import Apaexlinecolumn from "./apaexlinecolumn"


class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
 
            sold: 0, 
            breadcrumbItems : [],
            reports : []
        }
    }
    componentDidCatch(error) {
        // Changing the state to true
        // if some error occurs
        console.log(error)
      }
    async componentDidMount() {
 
        const requestOptions = {
               method: 'GET',
               headers: { 'Content-Type': 'application/json' },
           };
           await fetch(`${BaseUrl}/api/jobs/dashboard/`,requestOptions)
               .then(res => res.json())
               .then(
                   (data) => {

                       let reports = [{ icon : "ri-bookmark-3-fill", title : "Number of Bidding Jobs", value : data.bidding , rate : data.job_total, desc : "From previous period" },
                        { icon : "ri-store-2-line", title : "Number of Delivered Jobs", value : data.delivered, rate : data.job_total, desc : "From previous period" },
                        { icon : "ri-exchange-dollar-fill", title : "Number of Sold Jobs", value : data.sold, rate : data.job_total, desc : "From previous period" },
                        { icon : "ri-close-circle-fill", title : "Number of Lost Jobs", value : data.lost, rate : data.job_total, desc : "From previous period" },
                        { icon : "ri-briefcase-4-line", title : "Number of Completed Jobs", value : data.completed, rate : data.job_total, desc : "From previous period" },
                        ]

                   this.setState({ sold:data.sold, reports:reports })
                   },
                   (error) => {
                       console.log(error, 'error--------------') 
                   }
               )  
     }

    render() {
        const {reports } = this.state
        return (
            <React.Fragment>
                <div className="page-content">
                    <Container fluid>

                    <Breadcrumbs title="Dashboard" breadcrumbItems={[]} />
                        <Row>
                            <Col xl={12}>
                                <Row>
                                    <MiniWidgets reports={reports} />
                                </Row>
                            </Col>
                            <Col xl={6}>
                            <RevenueAnalytics/>
                            <Row>
                                <Col xl={6}>
                                    <EarningReports />
                                </Col>
                                <Col xl={6}>
                                    <SoldByUser />
                                </Col>
                            </Row>
                            
                            
                            </Col>

                            <Col xl={6}>
                              
                                <SalesAnalytics />

                                <Card>
                                    <CardBody>
                                        <CardTitle className="mb-4"> Sold Jobs By Month </CardTitle>
                                        <Apaexlinecolumn />
                                    </CardBody>
                                </Card>
                                
                            </Col>
     
                        </Row>                       

                    </Container> 
                </div>
            </React.Fragment>
        );
    }
}

export default Dashboard;
