import React, { Component } from 'react';
import { Card, CardBody, Row, Col, Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from "reactstrap";
//Import Charts
import ReactApexChart from 'react-apexcharts';
import "./dashboard.scss";
import { BaseUrl } from "../../helpers/Baseurl"

class SoldJobCompanyReports extends Component {
    constructor(props) {
    super(props);
    this.state = {
        user:"",
        user_options:[],
        menu : false,
        series: [35],
        sold:0,
        options: {
            chart: {
                sparkline: {
                    enabled: true
                }
            },
            dataLabels: {
                enabled: false
            },
            colors: ['#5664d2'],
            stroke: {
                lineCap: 'round'
            },
            plotOptions: {
                radialBar: {
                    hollow: {
                        margin: 0,
                        size: '70%'
                    },
                    track: {
                        margin: 0,
                    },

                    dataLabels: {
                        show: false
                    }
                }
            }
        },
    
        }
    }
    componentDidMount () {
        this.loadData()
    }
    async loadData(){

        const requestOptions = {
          method: 'GET',
          headers: { 'Content-Type': 'application/json' },
        };
        await fetch(`${BaseUrl}/api/jobs/sold_by_user/`,requestOptions)
            .then(res => res.json())
            .then(
                (data) => {
                    console.log(data, '-data')
                    let user = ""
                    if ((data.users).length > 0){
                        user = data.users[0].email
                    } 
                    this.setState({ 
                        user:user,
                        user_options:data.users,
                        series:[data.series],
                        sold:data.count
                    
                    })           
                   
                },
                (error) => {
                    console.log(error, 'error--------------') 
                }
            )  
       }

    async handleSoldJob(id, email){
        const requestOptions = {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' },
          };
          await fetch(`${BaseUrl}/api/jobs/sold_by_user/${id}`,requestOptions)
              .then(res => res.json())
              .then(
                  (data) => {
                      this.setState({ 
                          user:email,
                        user_options:data.users,
                        series:[data.series],
                        sold:data.count
                      
                      })           
                     
                  },
                  (error) => {
                      console.log(error, 'error--------------') 
                  }
              )  
    }


    render() {
        const { user_options, series, sold, user } = this.state
        return (
            <React.Fragment>
                <Card>
                    <CardBody>
                        <Dropdown className="float-end" isOpen={this.state.menu} toggle={() => this.setState({ menu: !this.state.menu })} >
                                <DropdownToggle tag="i" className="arrow-none card-drop" >
                                    <i className="mdi mdi-dots-vertical"></i>
                                </DropdownToggle>
                                <DropdownMenu className="dropdown-menu-end">
                                    {user_options.map(comp => {
                                        return <DropdownItem  key={comp.id} onClick={() => this.handleSoldJob(comp.id, comp.email)}>{comp.email}</DropdownItem>
                                    })}
                                </DropdownMenu>
                            </Dropdown>

                        <h4 className="card-title mb-4">Sold Jobs by User</h4>
                        <div className="mb-3">
                        </div>
                        <div className="text-center">
                            <Row>
                                <Col sm={12}>
                                    <div>
                                        <div className="mb-3">
                                            <div id="radialchart-1" className="apex-charts">
                                                <ReactApexChart options={this.state.options} series={series} type="radialBar" height="60" />
                                            </div>
                                        </div>

                                        <p className="text-muted text-truncate mb-2">By User</p>
                                        <p>{user}</p>
                                        <h5>{sold}</h5>
                                    </div>
                                </Col>
                            </Row>

                        </div>
                    </CardBody>
                </Card>
            </React.Fragment>
        );
    }
}

export default SoldJobCompanyReports;