// ACtual COST API's
  async MaterialActualSaveCell(oldValue, newValue, row, column){
    const id =this.props.match.params.id
    const row_id = ""+row.id;
    const strFirstThree = row_id.substring(0,3);
    let data = {
      "item": row.item,
      "job": id
    }

    if(!isNaN(parseFloat(row.unit_price)) && !isNaN(parseInt(row.quantity)) ){
      row.total_price = (parseInt(row.quantity) * parseFloat(row.unit_price)).toFixed(2);
      data["unit_price"] = parseFloat(row.unit_price).toFixed(2)
      data["quantity"] = parseInt(row.quantity)
      data["total_price"] = parseFloat(row.total_price).toFixed(2)
     }else{
      row.total_price = ''
     }

     if(!isNaN(parseFloat(row.unit_price)) && !isNaN(parseInt(row.quantity)) && !isNaN(parseFloat(row.total_price)) && row.item !=='' && row.item !==null ){
      if(strFirstThree === "new"){
        const requestOptions = {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(data)
          
          };
          fetch(`${BaseUrl}/api/jobs/material-actual/`,requestOptions)
                .then(res => 
                    {if(res.status >=400){
                        res.json()
                        .then(
                            (data) =>{  
                              console.log(data, "---data")
                              
                            }
                        )
                    }
                    else{
                        res.json()
                        .then(
                            (data) =>{
                              const { Material_Actual } = this.state
                              let updated_data = this.state.Material_Actual
                              for (let i=0; i<Material_Actual.length;i++ ){
                                if (updated_data[i].id === row.id){
                                  updated_data[i].id = data.id
                                }
                                
                              }
                              this.setState({ Material_Actual:updated_data })
                            }
                        )
                    }    
                }
            )
      } 
      else{
        const requestOptions = {
          method: 'PUT',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify(data)
          
          };
          fetch(`${BaseUrl}/api/jobs/material-actual/${row_id}/`,requestOptions)
                .then(res => 
                    {if(res.status >=400){
                        res.json()
                        .then(
                            (data) =>{  
                              console.log(data, "---data")
                              
                            }
                        )
                    }
                    else{
                        res.json()
                        .then(
                            (data) =>{
                              const { Material_Actual } = this.state
                              let updated_data = this.state.Material_Actual
                              if( data.total_price !== null || data.total_price !== 0){
                                
                                for (let i=0; i<Material_Actual.length;i++ ){
                                  if (updated_data[i].id === row.id){ 
                                    updated_data[i].total_price = data.total_price
                                  }
                                }
                              }
                              this.setState({ Material_Actual:updated_data })
                                                        
                            }
                        )
                    }    
                }
            )
      }
    }
    
  }

