import React, { Component } from "react";

import { Row, Col, Card, CardBody, FormGroup, Button, Label, Container } from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'

//Import Breadcrumb
import Breadcrumbs from '../../components/Common/Breadcrumb';
 import { BaseUrl } from "../../helpers/Baseurl";

class FormValidations extends Component {
  constructor(props) {
    super(props);
    if (localStorage.getItem("authUser")) {
        const obj = JSON.parse(localStorage.getItem("authUser"));
        const is_superuser = obj.user.is_superuser
        if (!is_superuser){
          this.props.history.push('/dashboard')
          }
      }
    this.state = {
      breadcrumbItems: [
        { title: "Users", link: "/users" },
        { title: "Add User", link: "#" },
      ],
      user:[]
    };
    toastr.options = {
      positionClass: "toast-top-right",
      closeButton: "true",
      progressBar: "true",
      showEasing: "swing",
      hideEasing: "linear",
      showMethod: "fadeIn",
      hideMethod: "fadeOut",
      showDuration: 300,
      hideDuration: 1000,
      timeOut: 5000,
      extendedTimeOut: 1000

  }
    this.handleSubmit = this.handleSubmit.bind(this);

  }

  componentDidCatch(error) {
    // Changing the state to true
    // if some error occurs
    console.log(error)
    toastr.error("Something Went Wrong", "")  
    this.props.history.push('/users')
  }


  async componentDidMount() {
      const id =this.props.match.params.id
 
    const requestOptions = {
           method: 'GET',
           headers: { 'Content-Type': 'application/json' },
       };

       await fetch(`${BaseUrl}/api/account/users/${id}/`,requestOptions)
           .then(res => res.json())
           .then(
               (data) => {
                   this.setState({ user: data  })
               },
               (error) => {
                   console.log(error, 'error--------------')
               }
           )  
 }
  
  handleSubmit(e,  values) {
    const { user } = this.state
      let data = {"email": values.email,
                  "first_name": values.first_name,
                  "last_name": values.last_name,
                  "phone":values.phone}   
        if(values.password !== "" && values.password !== undefined && values.password  === values.password1){
          data["password"]=values.password

        }else{
          data["password"]=user.password

        }
      
      
      const requestOptions = {
            method: 'PUT',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(data)
        };
        fetch(`${BaseUrl}/api/account/users/${user.id}/`,requestOptions)
              .then(res => 
                  {if(res.status >= 400){
                      res.json()
                      .then(
                          (data) =>{  
                            toastr.error("Something Went Wrong", "")  }
                      )
                  }
                  else{
                      res.json()
                      .then(
                          (data) =>{
                            this.setState({ user:data})
                          toastr.success(`Updated Successfully`, "")  
                          }
                      )
                  }    
              }
          )
  }

  render() {
    const { user } = this.state

    
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid={true}>



            <Breadcrumbs title="Profile" breadcrumbItems={this.state.breadcrumbItems} />

            <Row>
              <Col lg={12}>
                <Card>
                  <CardBody>
                               

                    <AvForm onValidSubmit={this.handleSubmit}>
                       <Row>
                          <Col md="6">
                            <div className="mb-3">
                              <Label className="form-label" htmlFor="first_name">First Name</Label>
                              <AvField
                                name="first_name"
                                placeholder="First Name"
                                type="text"
                                errorMessage="Enter First Name"
                                className="form-control"
                                id="first_name"
                                value={user.first_name}
                              />
                            </div>
                          </Col>
                          <Col md="6">
                            <div className="mb-3">
                              <Label className="form-label" htmlFor="last_name">Last Name</Label>
                              <AvField
                                name="last_name"
                                placeholder="Last Name"
                                type="text"
                                errorMessage="Enter Last Name"
                                className="form-control"
                                id="last_name"
                                value={user.last_name}
                              />
                            </div>
                          </Col>
                        </Row>
          
                        <Row>
                          <Col md="6">
                            <div className="mb-3">
                              <AvField
                                name="email"
                                label="E-Mail"
                                placeholder="Enter Valid Email"
                                type="email"
                                errorMessage="Invalid Email"
                                value={user.email}
                                disabled
                              />
                            </div>
                          </Col>
                           <Col md="6">
                            <div className="mb-3">
                              <AvField
                                name="phone"
                                label="Contact No."
                                placeholder="Enter Only number"
                                type="number"
                                errorMessage="Enter Only Number"
                                value={user.phone}
                                validate={{
                                  pattern: {
                                    value: "^[0-9]+$",
                                    errorMessage: "Only Numbers"
                                  },
                                  maxLength: {
                                    value: 15,
                                    errorMessage: "Numbers must be less than 16 digit"},
                                  minLength: {
                                    value: 10,
                                    errorMessage: "Numbers must be Grater than 9 digit"}    
                                }}
                                
                              />
                            </div>
                          </Col>
                         </Row>
                        
                            <Row>
                            <Col md="6">  
                              <div className="mb-3">
                                <Label>Change Password</Label>
                                <AvField
                                  name="password"
                                  type="password"
                                  placeholder="Password"
                                  autoComplete="new-password"
                                  errorMessage="Enter password"
                                />
                                </div>
                              </Col>
                              <Col md="6">
                                <div className="mb-3">
                                <Label>Confirm Password</Label>
                                <AvField
                                  name="password1"
                                  type="password"
                                  placeholder="Re-type Password"
                                  errorMessage="Enter Re-password"
                                  validate={{
                                    match: { value: "password" }
                                  }}
                                />
                                </div>
                              </Col>
                          </Row>
                      
                        

                      <FormGroup className="mb-0">
                        <div>
                          <Button type="submit" color="primary" className="me-1">
                            Submit
                        </Button>{" "}
                          <Button type="button" onClick={()=> this.props.history.push('/users')} color="secondary">
                            Cancel
                        </Button>
                        </div>
                      </FormGroup>
                    </AvForm>
                  </CardBody>
                </Card>
              </Col>
            </Row>

          </Container>
        </div>
      </React.Fragment>
    );
  }
}

export default FormValidations;
