import React, { Component } from "react"
import { Button, Card, CardBody, Col, Container, Row } from "reactstrap"
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit"
import BootstrapTable from "react-bootstrap-table-next"
import paginationFactory, {
  PaginationListStandalone,
  PaginationProvider,
} from "react-bootstrap-table2-paginator"
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'

import { Link } from "react-router-dom"

//Import Breadcrumb
import Breadcrumbs from "../../components/Common/Breadcrumb"

import { BaseUrl } from "../../helpers/Baseurl"


class Customers extends Component {
  constructor(props) {
    super(props)
    if (localStorage.getItem("authUser")) {
      const obj = JSON.parse(localStorage.getItem("authUser"));
      const is_superuser = obj.user.is_superuser
      if (!is_superuser){
        this.props.history.push('/dashboard')
        }
    }
    this.state = {
      customers: [],
      breadcrumbItems: [
          { title : "DynamicOutdoors", link : "/dashboard" },
          { title : "Users", link : "#" }
      ]
    }

    this.handleDelete = this.handleDelete.bind(this)

  }
  

  async componentDidMount() {
 
    this.loadData();
  }
  componentDidCatch(error) {
    // Changing the state to true
    // if some error occurs
    console.log(error)
    toastr.error("Something Went Wrong", "")
    this.props.history.push('/dashboard')
  }

  async loadData(){
    const requestOptions = {
      method: 'GET',
      headers: { 'Content-Type': 'application/json' },
  };

  await fetch(`${BaseUrl}/api/account/getusers/`,requestOptions)
      .then(res => res.json())
      .then(
          (data) => {
              this.setState({ customers: data  })
          },
          (error) => {
              console.log(error, 'error--------------')
             
          }
      ) 
  }

  handleDelete = id => {
    const requestOptions = {
      method: 'DELETE',
    };

    fetch(`${BaseUrl}/api/account/users/${id}/`,requestOptions)
    .then(res => 
            {if(res.status >=400 ){
              toastr.error("Something Went Wrong", "")

            }
            else{
                 this.loadData()
                }    
            }
          )
  }

  render() {
    //get Customets
    const { customers } = this.state
   
   
    // Columns
    const EcommerceCustomerColumns = [
      {
        dataField: "first_name",
        text: "Username",
        sort: true,
        formatter: (cellContent, row) => (
          <>
            <p className="mb-0">{`${row.first_name} ${row.last_name}`}</p>
          </>
        ),
      },
      {
        text: "Email",
        dataField: "email",
        sort: true,
        formatter: (cellContent, row) => (
          <>
            <p className="mb-0">{row.email}</p>
          </>
        ),
      },
      {
        text: "Phone",
        dataField: "phone",
        sort: true,
      },
      {
        dataField: "menu",
        isDummyField: true,
        text: "Action",
        formatter: (cellContent, row) => 
          { if (row.is_superuser){
            return ("")
          }else{
            return (
              <>
              <Link  to="#" onClick={() => this.props.history.push(`/update-user/${row.id}`)} className="me-3 text-primary"><i className="mdi mdi-pencil font-size-18"></i></Link>
              <Link to="#" onClick={() => this.handleDelete(row.id)} className="text-danger"><i className="mdi mdi-trash-can font-size-18"></i></Link>
              </>)
          }
        }
      },
    ]
    
    //pagination customization
    const pageOptions = {
      sizePerPage: 10,
      totalSize: customers.length, // replace later with size(customers),
      custom: true,
    }

    const defaultSorted = [{
      dataField: 'id',
      order: 'desc'
    }];

    const { SearchBar } = Search

    const selectRow = {
      mode: 'checkbox'
    };

    return (
      <React.Fragment>

        <div className="page-content">
          
          <Container fluid>
          <Breadcrumbs title="Users" breadcrumbItems={this.state.breadcrumbItems} />

            <Row>
              <Col xs="12">
                <Card>
                  <CardBody>
                    <PaginationProvider
                      pagination={paginationFactory(pageOptions)}
                      keyField='id'
                      data={customers}
                    >
                      {({ paginationProps, paginationTableProps }) => (
                        <ToolkitProvider
                          keyField='id'
                          columns={EcommerceCustomerColumns}
                          data={customers}
                          search
                        >
                          {toolkitProps => (
                            <React.Fragment>
                              <Row>
                                <Col sm="4">
                                  <div className="search-box me-2 mb-2 d-inline-block">
                                    <div className="position-relative">
                                      <SearchBar
                                        {...toolkitProps.searchProps}
                                      />
                                      <i className="bx bx-search-alt search-icon" />
                                    </div>
                                  </div>
                                </Col>
                                <Col sm="8">
                                  <div className="text-sm-end">
                                    <Button
                                      type="button"
                                      color="success"
                                      className="btn-rounded mb-2 me-2"
                                      onClick={() => this.props.history.push("/add-user")}

                                    >
                                      <i className="mdi mdi-plus me-1" />{" "}
                                      New User
                                    </Button>
                                  </div>
                                </Col>
                              </Row>

                              <div className="table-responsive">
                                <BootstrapTable
                                  keyField={"id"}
                                  responsive
                                  bordered={false}
                                  striped={false}
                                  defaultSorted={defaultSorted}
                                  headerWrapperClasses={"thead-light"}
                                  {...toolkitProps.baseProps}
                                  {...paginationTableProps}
                                />

                              </div>
                              <div className="pagination pagination-rounded justify-content-end mb-2">
                                <PaginationListStandalone
                                  {...paginationProps}
                                />
                              </div>
                            </React.Fragment>
                          )}
                        </ToolkitProvider>
                      )}
                    </PaginationProvider>
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
      </React.Fragment>
    )
  }
}

export default Customers