import React, { Component } from "react";

import { Row, Col, Card, CardBody, FormGroup, Button,  Label, Input, Container, Spinner } from "reactstrap";
import { AvForm, AvField } from "availity-reactstrap-validation";
import Select from "react-select";
import toastr from 'toastr'
import 'toastr/build/toastr.min.css'
//Import Breadcrumb
import Breadcrumbs from '../../components/Common/Breadcrumb';
import { BaseUrl } from "../../helpers/Baseurl"

const optionGroup = [
	{ 
		label: "Status",
		options: [
			{ label: "Bidding", value: "bidding" },
      { label: "Delivered", value: "delivered" },
      { label: "Sold", value: "sold" },
      { label: "Lost", value: "lost" },
			{ label: "Completed", value: "completed" },
			
		]
	}
];

class AddJobs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      labour_rate:0.00,
      breadcrumbItems: [
        { title: "Jobs", link: "/jobs" },
        { title: "Add New Job", link: "#" },
      ],
      description:"",
      selectedGroup: { label: "Bidding", value: "bidding" },
      selectedCompanyGroup: "",
      companyGroup :""
      
    };
    toastr.options = {
      positionClass: "toast-top-right",
      closeButton: "true",
      progressBar: "true",
      showEasing: "swing",
      hideEasing: "linear",
      showMethod: "fadeIn",
      hideMethod: "fadeOut",
      showDuration: 300,
      hideDuration: 1000,
      timeOut: 5000,
      extendedTimeOut: 1000
  }
	

    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleSelectGroup = this.handleSelectGroup.bind(this);
    this.handleSelectCompanyGroup = this.handleSelectCompanyGroup.bind(this);
    

  }
  componentDidCatch(error) {
    // Changing the state to true
    // if some error occurs
    console.log(error)
    toastr.error("Something Went Wrong", "")
    this.props.history.push('/jobs')
  }
  convertToFloat() {
    let value = document.getElementById('labour_rate').value
    this.setState({ labour_rate:parseFloat(value).toFixed(2) });

  }
  async componentDidMount() {
 
    this.loadData();
 }

 async loadData(){

  const requestOptions = {
    method: 'GET',
    headers: { 'Content-Type': 'application/json' },
  };
  await fetch(`${BaseUrl}/api/jobs/company/`,requestOptions)
      .then(res => res.json())
      .then(
          (data) => {
            console.log(data , "-----------")
              let options = []
              data.map(comp => {
                options.push({ label: comp.name, value: comp.id })
              })
              let selectedCompanyGroup= ""
              if (options.length >0){
                selectedCompanyGroup = options[0]
              }else{
                this.props.history.push("/new-company")
              }
              let companyGroup = [
                { 
                  label: "Company",
                  options: options
                }
              ];
              this.setState({ companyGroup: companyGroup,
                selectedCompanyGroup: selectedCompanyGroup,
              
              })           
             
          },
          (error) => {
              console.log(error, 'error--------------') 
          }
      )  
 }
  

  handleSelectGroup = selectedGroup => {
    this.setState({ selectedGroup });
  };

  handleSelectCompanyGroup = selectedCompanyGroup => {
    this.setState({ selectedCompanyGroup });
  };

  handleSubmit(e, values) {
    this.setState({ loading: true })
    
    const { selectedGroup, selectedCompanyGroup,description } =  this.state
    const obj = JSON.parse(localStorage.getItem("authUser"));
    let data = {
      'status': selectedGroup.value,
      'company': parseInt(selectedCompanyGroup.value),
      'description':description,
      'created_by': obj.user.id,
      'date':new Date()
    }
    data['name'] = values.name
    data['labour_rate'] = parseFloat(values.labour_rate).toFixed(2)
    data["actual_gross_margin"] = ""
    data["target_selling_price"] = ""
    data["actual_selling_price"] = ""

    if(values.desired_gross_margin === "") 
    {
      data["desired_gross_margin"] = ""
    }else{data["desired_gross_margin"] = parseInt(values.desired_gross_margin)}
    console.log(data, "-----------------")

      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
        };
        fetch(`${BaseUrl}/api/jobs/jobs/`,requestOptions)
              .then(res => 
                  {if(res.status >= 400){
                      res.json()
                      .then(
                          (data) =>{  
                            console.log(data, "---data")
                            toastr.error("Something Went Wrong", "") 
                            this.setState({ loading: false })
                          }
                            
                      )
                  }
                  else{
                      res.json()
                      .then(
                          (data) =>{
                          toastr.success(`Job Created Successfully`, "") 
                          this.props.history.push(`/edit-job/${data.id}`) 
                          }
                      )
                  }    
              }
          )

  }

  render() {
    const { selectedGroup, selectedCompanyGroup, companyGroup } = this.state;
    return (
      <React.Fragment>
        <div className="page-content">
          <Container fluid={true}>
            <Breadcrumbs title="Add New Job" breadcrumbItems={this.state.breadcrumbItems} />
            <AvForm className="needs-validation" onValidSubmit={this.handleSubmit} >
              <Row>
                <Col xl="6">
                  <Card>
                    <CardBody> 
                      <h4 className="card-title">Job Detail</h4>
                        <Row>
                          <Col md="6">
                            <div className="mb-3">
                              <Label className="form-label " htmlFor="name">Job Name</Label>
                              <AvField
                                name="name"
                                placeholder="Job Name"
                                type="text"
                                errorMessage="Enter First Name"
                                className="form-control"
                                validate={{ required: { value: true } }}
                                id="name"
                                
                              />
                            </div>
                          </Col> 
                          <Col md="6">
                        <div className="mb-3">
                          <Label className="form-label">Company</Label>
                          <Select 
                            value={selectedCompanyGroup}
                            onChange={this.handleSelectCompanyGroup}
                            options={companyGroup}
                            classNamePrefix="select2-selection"
                          />

                        </div>
                      </Col>
                      
                        </Row>
                        <Row>
                        <Col md="6">
                            <div className="mb-3">
                                <AvField
                                name="labour_rate"
                                value={this.state.labour_rate}
                                label="Labor Rate"
                                placeholder="Labor Rate"
                                type="number"
                                errorMessage="Enter Only Number"
                                onBlur={() => this.convertToFloat()}
                                validate={{
                                    required: { value: true },
                                    
                                }}
                                />
                            </div>
                          </Col>
                          <Col md="6">
                            <div className="mb-3">
                              <AvField
                                name="desired_gross_margin"
                                label="Desired Gross Margin"
                                placeholder="Desired Gross Margin"
                                type="number"
                                errorMessage="Enter Only Number"
                                validate={{
                                    pattern: {
                                    value: "^[0-9]+$",
                                    errorMessage: "Only Numbers"
                                    }
                                }}
                              />
                            </div>
                          </Col>
                       
                        
                        </Row>
                       
                        <Row>
                        
                          <Col md="12">
                            <div className="mb-3">
                              <Label className="form-label">Job Description</Label>
                              <Input
                                type="textarea"
                                id="textarea"
                                name= "description"
                                rows="3"
                                value={this.state.description}
                                placeholder="Job Description."
                                onChange={(e)=> this.setState({description:e.target.value})}
                              />

                            </div>
                          </Col>
                        
                        </Row>
                        <Row>
                      
                        <Col md="6">
                            <div className="mb-3">
                              <Label className="form-label">Job Status</Label>
                              <Select 
                                value={selectedGroup}
                                onChange={this.handleSelectGroup}
                                options={optionGroup}
                                classNamePrefix="select2-selection"
                              />

                            </div>
                          </Col>
                    
                    </Row>
                        
                        <FormGroup className="mb-0">
                          {this.state.loading?(
                            <Spinner className="me-2 mt-2" color="primary" />
                          ):(
                            <div>
                        
                          <Button type="submit" color="primary" className="me-1">
                            Submit
                        </Button>{" "}
                          <Button type="button" onClick={() => this.props.history.goBack()} color="secondary">
                            Cancel
                        </Button>
                        </div>
                          )}
                        
                        
                      </FormGroup>      

                    </CardBody>
                  </Card>
                </Col>
              </Row>
            </AvForm>

          </Container>
        </div>
      </React.Fragment>
    );
  }
}

export default AddJobs;



    